p{
  word-wrap: break-word;         /* All browsers since IE 5.5+ */
  overflow-wrap: break-word;     /* Renamed property in CSS3 draft spec */
}
.text-blue{
  color:  #1D69A7 !important;
}
.text-grey{
  color: #8E8E8E !important;
}
.bg-blue{
  background-color: #1D69A7;
}
.bg-lightgrey{
  background-color: #F6FAF6 !important;
}
.bg-lightblue{
  background-color: #EFEFFF !important;
}
.bg-black{
  background-color: black !important;
}
.btn-blue{
  background-color: #1D69A7;
  color: white;
  padding: 0.3rem 0.75rem;
  border : none !important;
  border-radius: 4px;
}
.btn-white{
  background-color: white;
  color: black;
  padding: 0.3rem 0.75rem;
  border : none !important;
  border-radius: 4px;
}
.p-relative{
  position: relative !important;
}
.p-absolute{
  position: absolute !important;
  
}
.d-web{
  display: block !important;
}
.d-mob{
  display: none !important;
}
button , button:focus{
  outline: none !important;
  box-shadow: none !important;
}
.text-webkit-center{
  text-align: -webkit-center !important;
}
.MuiInputLabel-outlined.MuiInputLabel-marginDense {
  transform: translate(14px, 7px) scale(1);
}
.checkBox-cutom-label span{
  font-size: 12px !important;
}
.cutom-pincode input{
  border : none !important;
  border-bottom-left-radius: 0px !important;
    border-bottom-right-radius: 0px !important;
  border-bottom : 2px solid grey !important;
  margin-right: 1.5rem;
  font-size: 30px !important;
}
.cutom-pincode input:last-child{
  margin-right:0rem !important;
}
.cutom-pincode input:focus{
  color : #1D69A7 !important;
  border-bottom : 2px solid  #1D69A7!important;
}
.loader{
  background-color: #1d69a72c;
  position: fixed;
  z-index: 12345;
  height: 100vh;
  width: 100vw;
  text-align: center !important;
  top: 0px;
  left: 0px;
  padding-top: 15rem;
  background-color: rgba(0, 0, 0, 0.507);
}
@media (min-width: 1200px){
  .container, .container-lg, .container-md, .container-sm, .container-xl {
      max-width: 1180px !important;
  }
}
@media (max-width: 540px){
      .d-web{
        display: none !important;
      }
      .d-mob{
        display: block !important;
      }
      .cutom-pincode input{
        width: 55px !important;
        margin-right: 0.5rem;
        font-size: 25px !important;
      }
}
@media (max-width: 320px){
  .cutom-pincode input{
    width: 40px !important;
    margin-right: 0.5rem;
    font-size: 25px !important;
  }
}